// Jump to content
.jump-to-content {
    display: block;
    background: #fff;
    position: absolute;
    overflow: hidden;
    text-align: center;
    height: 0;

    &:focus {
        position: initial;
        background: #fcaf15;
        padding: 0.5rem;
        height: auto;
        color: black;
    }
}

/* ****************************************** */
/*   Old padding boxes from regionhalland.se  */
/*   TODO: Remove once unused by site         */
/* ****************************************** */

.rh-xpad-A {
    padding-left: 0; padding-right: 0;

    @media (min-width: $breakpoint-sm) {
        padding-left:   $space-2; padding-right:  $space-2;
    }

    @media (min-width: $breakpoint-md) {
        padding-left:   $space-3; padding-right:  $space-3;
    }
}

.rh-xpad-B {
    padding-left: $space-2; padding-right: $space-2;

    @media (min-width: $breakpoint-sm) {
        padding-left:   $space-2; padding-right:  $space-2;
    }

    @media (min-width: $breakpoint-md) {
        padding-left:   $space-3; padding-right:  $space-3;
    }
}

.rh-xpad--left {
    padding-left: 0;
    padding-right: 0;

    @media (min-width: $breakpoint-md) {
        padding-left: $space-3;
        padding-right: $space-3;
    }

    @media (min-width: $breakpoint-lg) {
        padding-left: $space-3;
        padding-right: 0;
    }
}

/* ************************************ */
