* {
    box-sizing: border-box;
}

body {
    font-family: proxima-nova, Arial; // Import of proxima happens in main.scss
}

.rh-block-on-mobile {
    display: block;

    @include medium {
        display: inline;
    }
}

#hidden{
    display: none;
}

.hidden-sm {
    display: none;

    @include medium {
        display: block;
    }
}

.only-sm {
    display: block;

    @include medium {
        display: none;
    }
}

.center{
      margin-left: auto;
      margin-right: auto;
}
